div.thumbnail {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  padding-top: 100%;
}

.btn {
    cursor: pointer;
    background-color: #000000;
    padding: 5px 10px 5px 10px;
    border-radius: 10%;
    margin-right: 5px;
}

#btn {
    text-decoration: none;
    color: rgb(255, 255, 255);
}

#btn:hover {
    color: #e74944;
    background-color: #000000;
}
.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

#search, #saved {
    color: #ffffff;
}

#search:hover, #saved:hover {
    color: #e74944;
}

#nav {
    background-color: black;
}
